const Hint = ({ cl, children, top, left }) => {
  return (  h('span',{'className':cl`Copy`, 'style':{ 'z-index': 2, position: 'absolute', top: top + 15 , left: left + 15 }},
    h('span',{'className':cl`Noise`}, children ),
  ))
}

const Letter = ({ file, cl, top, left, children, class: className, href }) => {
  const style = { top, left }
  // const style = { 'top': `${top}px`, 'left': `${left}px` }
  return (  h('a',{'className':cl`LetterA`, 'href':href, 'style':style},
    h(Hint,{cl:cl,top:top,left:left},children),
    h('img',{'className':className,'src':file,'style':style}),
  ))
}


